body {
	font-family: 'Poppins', sans-serif;
}

.contenedor {
	width: 100%;
	/* height: calc(100vh - 50px); */
	background-color: #fff;
	padding: 5px 20px;
}
.contenido {
	width: 100%;
	height: auto;
	border-top: 3px solid #ccc;
	padding-top: 10px;
}
.noticia-contenedor {
	margin-bottom: 10px;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
}
.noticia-contenedor img {
	max-width: 100%;
}
.noticia-contenido {
	border-bottom: 1px solid #ccc;
}
.noticia-datos {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	-webkit-align-items: center;
	        align-items: center;
	margin-top: 5px;
}
.noticia-autor,
.noticia-fecha {
	margin: 0;
	color: #00b894;
	font-weight: 700;
}
.noticia-titulo {
	text-align: left;
	line-height: 1.2;
}
.noticia-descripcion {
	font-size: 18px;
	margin-bottom: 40px;
}
.noticia-botones {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	-webkit-align-items: center;
	        align-items: center;
	margin-bottom: 10px;
}
.noticia-leer {
	background-color: #2d3436;
	color: #fff;
	padding: 0.5rem 1rem;
	border-radius: 30px;
	font-size: 20px;
	cursor: pointer;
	outline: none;
	border: none;
	text-decoration: none;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
}

.noticia-leer .icono {
	background-image: url(/static/media/arrow.8c2b3695.svg);
	background-position: center center;
	background-size: contain;
	background-repeat: no-repeat;
	width: 18px;
	height: 18px;
	margin-left: 10px;
	transition: all 0.1s ease;
}
.noticia-leer:hover .icono {
	margin-left: 15px;
}
.noticia-mas-tarde {
	border: none;
	outline: none;
	background-color: transparent;
	font-size: 18px;
	cursor: pointer;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
}
.noticia-mas-tarde span {
	font-size: 12px;
	color: rgb(141, 141, 141);
	opacity: 0;
	transition: all 0.3s ease;
}
.noticia-mas-tarde:hover span {
	opacity: 1;
}
.noticia-mas-tarde .icono {
	background-image: url(/static/media/despues.7c67df1e.svg);
	background-position: center;
	background-size: contain;
	background-repeat: no-repeat;
	width: 20px;
	height: 20px;
	margin-left: 5px;
	display: inline-block;
	transition: -webkit-transform 0.2s ease;
	transition: transform 0.2s ease;
	transition: transform 0.2s ease, -webkit-transform 0.2s ease;
}
.noticia-mas-tarde:hover .icono {
	-webkit-transform: scale(1.2);
	        transform: scale(1.2);
}

/** tablet **/
@media (min-width: 768px) {
	.contenido {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		grid-gap: 20px;
		gap: 20px;
	}
	.noticia-uno {
		grid-column: 1 / 3;
	}
	.noticia-uno img {
		width: 100%;
	}
	.noticias-dos-contenedor {
		grid-column: 1 / 3;
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		grid-gap: 20px;
		gap: 20px;
	}
}

/** buscador estra **/
.contenedor-buscador {
	background-color: #2d3436;
	padding: 5px 0;
}

/** desktop **/
@media (min-width: 1200px) {
	.contenido {
		display: grid;
		grid-template-columns: repeat(5, 1fr);
		grid-gap: 20px;
		gap: 20px;
	}
	.noticia-titulo {
		font-size: 18px;
	}
	.noticia-descripcion {
		font-size: 16px;
	}
	.noticia-uno {
		grid-column: 1 / 3;
	}
	.noticia-uno img {
		width: 100%;
	}
	.noticia-uno .noticia-leer {
		font-size: 16px;
		padding: 0.5rem 1rem;
	}

	.noticia-dos {
		border-bottom: none;
	}
	.noticia-dos img,
	.noticia-dos .noticia-datos {
		display: none;
	}

	.noticia-dos .noticia-leer {
		font-size: 14px;
		padding: 0.5rem 1rem;
	}
	.noticias-dos-contenedor {
		display: -webkit-flex;
		display: flex;
		-webkit-flex-direction: column;
		        flex-direction: column;
		-webkit-justify-content: space-between;
		        justify-content: space-between;
		grid-column: auto;
	}

	.noticia-tres .noticia-leer {
		font-size: 16px;
		padding: 0.5rem 1rem;
	}
}

